.rightBar{
    flex: 4;
    position: sticky;
    top: 70px;
    height: calc(100vh - 70px);
    overflow: scroll;
    &::-webkit-scrollbar{
        display: none;
    }

    .container{
        padding: 20px;

        .item{
            -webkit-box-shadow: 0px 0px 15px 1px rgba(0,0,0,0.09);
            -moz-box-shadow: 0px 0px 15px 1px rgba(0,0,0,0.09);
            box-shadow: 0px 0px 15px 1px rgba(0,0,0,0.09);
            padding: 20px;
            margin-bottom: 20px;

            span{
                color: gray;
            }

            .user{
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin: 10px 0px;
                
                .userInfo{
                    display: flex;
                    align-items: center;
                    gap: 15px;

                    img{
                        width: 35px;
                        height: 35px;
                        border-radius: 50%;
                        object-fit: cover;
                    }

                    p{
                        color: gray;
                    }

                    span{
                        font-weight: 700;
                        color: black;
                    }

                }

                .buttons{
                    display: flex;
                    align-items: center;
                    gap: 10px;

                    button{
                        border: none;
                        padding: 5px;
                        color: white;
                        cursor: pointer;

                        &:first-child{
                            background-color: #5271ff;
                        }

                        &:last-child{
                            background-color: #f0544f;
                        }
                    }

                }
            }

        }
    }
}